<template>
<div id="rentCar">
   <h2>用户如何取车、还车？</h2>
  <p class="actives">天天租用户取车</p>
  <p>用户取车目前存在两种方式：</p>
  <p>第一种：直接扫码取车，用户打开待提车订单详情，点击底部的扫码取车，扫车辆SN码即可取车成功。用户取车前商家需完成该车辆录入、放入电池、购买套餐等操作。若取车失败请参考具体的提示操作。</p>
  <p>第二种：通过提车码取车，订单详情右上角可点击查看提车码，商家需提前帮助用户在e换电APP内新增车辆、绑定电池，然后商家通过小程序后台的：工作台-扫码取车，扫提车码即可取车成功。</p>
   <p class="actives">天天租用户还车</p>
  <p>用户到店还车，需要出示订单详情，商家确认车辆无误后，若存在理赔需要提前处理完成。打开小程序后台：扫码还车，扫订单详情内的还车码即可还车成功，还车成功后用户押金在e换电APP-我的钱包处可退。</p>

</div>
</template>

<script>
    export default {
        name: "rentCar"
    }
</script>

<style scoped lang="scss">

</style>



